.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 19%;
    border-radius: 5px;
    margin: 0px auto;
  }
  .inner-container{
    display: flex;
  }
  p{
    margin: 0px;
  }
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
  .card-icon {
    padding: 2px 0px 0px 10px;
  }
  .card-title{
    display: flex;
    justify-content: center;
  }
  
  .container {
    padding: 2px 16px;
  }

  .container2{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    padding: 10px;
  }